import React, { useState, useEffect } from "react";
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import IconButton from '@mui/material/IconButton';
import Paper from '@mui/material/Paper';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import Typography from '@mui/material/Typography';
import Collapse from '@mui/material/Collapse';
import { TableVirtuoso } from 'react-virtuoso';
import Checkbox from '@mui/material/Checkbox';
import Modal from '@mui/material/Modal';
import Preview from './Preview';
import Box from '@mui/material/Box';
import PreviewIcon from '@mui/icons-material/Preview';


const sample = [
  ['Frozen yoghurt', 159, 6.0, 24, 4.0],
  ['Ice cream sandwich', 237, 9.0, 37, 4.3],
  ['Eclair', 262, 16.0, 24, 6.0],
  ['Cupcake', 305, 3.7, 67, 4.3],
  ['Gingerbread', 356, 16.0, 49, 3.9],
];

function createData([select, model_id, rmse, mse, mae, rmsle, mean_residual_deviance, params]) {
  return {
    select,
    model_id,
    rmse,
    mse,
    mae,
    rmsle,
    mean_residual_deviance,
    params,
  };
}

const columns = [
  {
    width: 30,
    label: 'select',
    dataKey: 'select',
  },
  {
    width: 500,
    label: 'model_id',
    dataKey: 'model_id',
  },
  {
    width: 150,
    label: 'rmse',
    dataKey: 'rmse',
  },
  {
    width: 140,
    label: 'mse',
    dataKey: 'mse',
  },
  {
    width: 140,
    label: 'mae',
    dataKey: 'mae',
  },
  {
    width: 140,
    label: 'rmsle',
    dataKey: 'rmsle',
  },
  {
    width: 160,
    label: 'mean_residual_deviance',
    dataKey: 'mean_residual_deviance',
  },
  {
    width: 70,
    label: 'parameters',
    dataKey: 'parameters',
  },
];


export default function CollapsibleTable2({response, onSelectChange}) {
    const [selected, setSelected] = useState([]);
    const [expandedRows, setExpandedRows] = useState([]);
    const [openModal, setOpenModal] = useState(false);
    const [ID, setID] = useState();

    const handleSelect = (event, id) => {
      const selectedIndex = selected.indexOf(id);
      const newSelected = [...selected];
    
      if (selectedIndex === -1) {
        newSelected.push(id);
      } else {
        newSelected.splice(selectedIndex, 1);
      }
    
      setSelected(newSelected);
      onSelectChange(newSelected);
    };

    const isSelected = (id) => selected.indexOf(id) !== -1;
    const isRowExpanded = (model_id) => expandedRows.includes(model_id);

    const handleRowExpand = (model_id) => {
      const isExpanded = expandedRows.includes(model_id);
      const newExpandedRows = isExpanded
        ? expandedRows.filter((id) => id !== model_id)
        : [...expandedRows, model_id];
      setExpandedRows(newExpandedRows);
    };

  const VirtuosoTableComponents = {
    Scroller: React.forwardRef((props, ref) => (
      <TableContainer component={Paper} {...props} ref={ref} />
    )),
    Table: (props) => (
      <Table {...props} sx={{ borderCollapse: 'separate', tableLayout: 'fixed' }} />
    ),
    TableHead,
    TableRow: ({ item: _item, ...props }) => <TableRow {...props} />,
    TableBody: React.forwardRef((props, ref) => <TableBody {...props} ref={ref} />),
  };
  const rows = [];
  const keys = [];
  for (let key in response) {
    keys.push(key);
  }
  for (let i = 0; i < keys.length; i++) {
    const row = [false];
    for (let key in response) {
        row.push(response[key][i]);  
    }
    const rowTemp = createData(row);
    rows.push(rowTemp);
  }


  function fixedHeaderContent() {
    return (
      <TableRow>
        {columns.map((column, index) => (
          <TableCell
            key={index}
            variant="head"
            align={column.numeric || false ? 'right' : 'left'}
            style={{ width: column.width }}
            sx={{
              backgroundColor: 'background.paper',
            }}
          >
            {column.label}
          </TableCell>
        ))}
      </TableRow>
    );
  }

  function rowContent(_index, row) {
    return (
      <React.Fragment>
        <TableCell padding="checkbox" style={{ width: columns[0].width }}>
            <Checkbox
              color="primary"
              align="center"
              // onClick={(event) => handleSelect(event, row.model_id)}
              // checked={isSelected(row.model_id)}
            />
          </TableCell>
          {columns.slice(1,-1).map((column, index) => (
            <TableCell
              key={index}
              align={column.numeric || false ? "right" : "left"}
              style={{
                width: column.width,
                padding: "16px",
              }}
            >
              <Box width="100%">{row[column.dataKey]}</Box>
            </TableCell>
          ))}

          <TableCell padding="checkbox" style={{ width: columns[columns.length - 1].width }}>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => {
              handleRowExpand(row.model_id);
            } }
          >
          <PreviewIcon margin="auto" onClick={() => {
            setOpenModal(true);
            setID(row.model_id);
          }}/>
          </IconButton>
        </TableCell>
        
      </React.Fragment>
    );
  }

  return (
    <div>
        <Paper style={{ height: 400, width: '100%' }}>
        <TableVirtuoso
          data={rows}
          components={VirtuosoTableComponents}
          fixedHeaderContent={fixedHeaderContent}
          itemContent={rowContent}
          />
      </Paper>
      <Modal
        open={openModal}
        onClose={() => setOpenModal(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
        >
        <Preview setOpenModal={setOpenModal} data={ rows.filter(row => row.model_id === ID)}/>
      </Modal>
    </div>
  );
}
